@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.custom-navbar {
    background-color: #001f3f; /* Warna biru dongker */
  }
  
  .text-white {
    color: white !important;
  }
  
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.spaced-content {
  margin-top: 60px; /* Add space between NavigationBar and SearchBar */
}

.spaced-content > *:not(:last-child) {
  margin-bottom: 15px; /* Add space between SearchBar and SearchResults */
}

body {
  font-family: 'Poppins', sans-serif;
}

h1, h2, h3 {
  font-weight: 10; /* Bold untuk heading */
}

p, span {
  font-weight: 100; /* Normal untuk teks biasa */
  line-height: 1.6; /* Tambahkan keterbacaan */
}

.footer {
    background-color: #222;
    color: #fff;
    text-align: center;
    padding: 10px 0;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}
