@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.spaced-content {
  margin-top: 60px; /* Add space between NavigationBar and SearchBar */
}

.spaced-content > *:not(:last-child) {
  margin-bottom: 15px; /* Add space between SearchBar and SearchResults */
}

body {
  font-family: 'Poppins', sans-serif;
}

h1, h2, h3 {
  font-weight: 10; /* Bold untuk heading */
}

p, span {
  font-weight: 100; /* Normal untuk teks biasa */
  line-height: 1.6; /* Tambahkan keterbacaan */
}
